@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

.services {
    width: 100%;
    height: 130vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: rgb(255,255,255);
    background: linear-gradient(180deg, rgba(255,255,255,1) 0%, rgba(251,251,251,1) 100%);
}
.title {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 18%;
    font-size: 2vw;
    color: #264653;
    font-family: "Inria Serif", serif;
    font-weight: 400;
    font-style: normal;
    position: relative;
    top: 7%;
}
.underline {
    position: relative;
    top: 7%;
    width: 8%;
}
.servicesContainer {
    position: relative;
    top: 15%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.servicesImg {
    width: 80%;
}
.servicesCards {
    position: absolute;
    display: flex;
    justify-content: space-around;
    width: 80%;
    top: 85%;
}
.card {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    box-shadow: 0px 0px 2px rgba(0, 0, 0, 0.2);
    background: white;
    width: 25%;
    height: 45vh;
    border-radius: 18px;
}
.icon {
    width: 4.5vw;
    height: 4.5vw;
    background: #343434;
    border-radius: 15px;
    position: absolute;
    bottom: 90%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.icon img {
    width: 50%;
}
.card h1 {
    font-family: "Roboto", serif;
    font-weight: 600;
    font-style: normal; 
    font-size: 1.4vw;
    color: #343434;
    margin-top: 20%;
}
.card p {
    font-family: "Roboto", serif;
    font-weight: 300;
    font-style: normal; 
    font-size: 1.250vw;
    width: 85%;
    color: #909090;
}